import React from "react";

//style
import styles from "./Footer.module.scss";

//images
import logo from "../../assets/images/footer/Logo.png";
import instagram from "../../assets/images/footer/instagram.png";
import facebook from "../../assets/images/footer/facebook.png";

const Footer = () => {
  return (
    <footer className={styles.footer}>
      <div className={`${styles.inner} container`}>
        <div className={styles.logoSocials}>
          <img src={logo} alt="" />
          <div className={styles.socials}>
            <div className={styles.icons}>
              <img src={facebook} alt="" />
              <img src={instagram} alt="" />
            </div>
            <span className={styles.number}>+38 067 442 1696</span>
          </div>
        </div>
        <div className={styles.navWrapper}>
          <nav className={styles.nav}>
            <ul>
              <li>
                <a href="Услуги" className={styles.link}>
                  Услуги
                </a>
              </li>
              <li>
                <a href="Прайс" className={styles.link}>
                  Прайс
                </a>
              </li>
              <li>
                <a href="Блог" className={styles.link}>
                  Блог
                </a>
              </li>
              <li>
                <a href="Наши специалисты" className={styles.link}>
                  Наши специалисты
                </a>
              </li>
            </ul>
          </nav>
          <div className={styles.contacts}>
            <span className={styles.addresses}>ул. Джона Маккейна 1а</span>
            <span>График работы:</span>
            <p> пн-пт. 10.00 - 19.00 сб. 11.00 - 18.00</p>
          </div>
        </div>
      </div>
    </footer>
  );
};
export default Footer;
