import React from "react";

//style
import styles from "./MainLazerSH2.module.scss";

const MainLazerSH2 = () => {
  return (
    <section className={styles.section}>
      <div className="container">
        <h2 className={styles.title}>Лазерне шліфування</h2>
        <p className={styles.text}>
          Ми не шукаємо шаблонних рішень для вирішення проблеми, ми враховуємо
          кожну дрібницю, тому і пропонуємо оптимальне лікування
        </p>
        <button type="button" className={styles.button}>
          Перезвоните мне
        </button>
      </div>
    </section>
  );
};
export default MainLazerSH2;
