import React from "react";

//style
import styles from "./MainIntro.module.scss";

//images
import mainImg from "../../../assets/images/main/mainImg.png";

const HeaderNav = () => {
  return (
    <section className={styles.section}>
      {/* <img src={mainImg} alt="" className={styles.img} /> */}
      <div className={styles.groupBlocks}>
        <div className={styles.block}>
          <div className={styles.wrapperIcon}>
            <svg
              xlink="http://www.w3.org/1999/xlink"
              width="79"
              height="78"
              viewBox="0 0 79 78"
            >
              <image
                width="79"
                height="78"
                href="data:img/png;base64,"
              />
            </svg>
          </div>
          <p className={styles.text}>Европейское качество обслуживания</p>
        </div>
        <div className={styles.block}>
          <div className={styles.wrapperIcon}>
            <svg
              xlink="http://www.w3.org/1999/xlink"
              width="67"
              height="71"
              viewBox="0 0 67 71"
            >
              <image
                id="Vector_Smart_Object"
                data-name="Vector Smart Object"
                width="67"
                height="71"
                href="data:img/png;base64,"
              />
            </svg>
          </div>
          <p className={styles.text}>Особый подход к лечению каждого</p>
        </div>
        <div className={styles.block}>
          <div className={styles.wrapperIcon}>
            <svg
              xlink="http://www.w3.org/1999/xlink"
              width="46"
              height="70"
              viewBox="0 0 46 70"
            >
              <image
                width="46"
                height="70"
                href="data:img/png;base64,"
              />
            </svg>
          </div>
          <p className={styles.text}>Качественная диагностика</p>
        </div>
        <div className={styles.block}>
          <div className={styles.wrapperIcon}>
            <svg
              xlink="http://www.w3.org/1999/xlink"
              width="69"
              height="40"
              viewBox="0 0 69 40"
            >
              <image
                width="69"
                height="40"
                href="data:img/png;base64,"
              />
            </svg>
          </div>
          <p className={styles.text}>Современное оборудование</p>
        </div>
      </div>
    </section>
  );
};
export default HeaderNav;
