import React from "react";

//style
import styles from "./MainServices.module.scss";

const MainServices = () => {
  return (
    <section className={styles.section}>
      <div className="container">
        <h2 className={styles.title}>Услуги:</h2>
        <input type="text" placeholder="Поиск" className={styles.input} />
        <div className={styles.groupBlocks}>
          <div className={styles.block}>
            <div className={styles.wrapperIcon}>
              <svg
                xlink="http://www.w3.org/1999/xlink"
                width="72"
                height="75"
                viewBox="0 0 72 75"
              >
                <image
                  id="Vector_Smart_Object"
                  data-name="Vector Smart Object"
                  width="72"
                  height="75"
                  href="data:img/png;base64,"
                />
              </svg>
            </div>
            <p className={styles.text}>Дерматоонкология</p>
          </div>
          <div className={styles.block}>
            <div className={styles.wrapperIcon}>
              <svg
                xlink="http://www.w3.org/1999/xlink"
                width="79"
                height="88"
                viewBox="0 0 79 88"
              >
                <image
                  id="Vector_Smart_Object"
                  data-name="Vector Smart Object"
                  width="79"
                  height="88"
                  href="data:img/png;base64,"
                />
              </svg>
            </div>
            <p className={styles.text}>Эстетическая медицина</p>
          </div>
          <div className={styles.block}>
            <div className={styles.wrapperIcon}>
              <svg
                xlink="http://www.w3.org/1999/xlink"
                width="71"
                height="71"
                viewBox="0 0 71 71"
              >
                <image
                  id="Vector_Smart_Object"
                  data-name="Vector Smart Object"
                  width="71"
                  height="71"
                  href="data:img/png;base64,"
                />
              </svg>
            </div>
            <p className={styles.text}>Лазерная терапия</p>
          </div>
          <div className={styles.block}>
            <div className={styles.wrapperIcon}>
              <svg
                xlink="http://www.w3.org/1999/xlink"
                width="50"
                height="71"
                viewBox="0 0 50 71"
              >
                <image
                  id="Vector_Smart_Object"
                  data-name="Vector Smart Object"
                  width="50"
                  height="71"
                  href="data:img/png;base64,"
                />
              </svg>
            </div>
            <p className={styles.text}>Дерматология</p>
          </div>
        </div>
      </div>
    </section>
  );
};
export default MainServices;
