import React from "react";

//style
import styles from "./MainBlog.module.scss";

//images
import spectrocopia from "../../../assets/images/main/spectrocopia.png";
import dermotoskopia from "../../../assets/images/main/dermotoskopia.png";
import gistologia from "../../../assets/images/main/gistologia.png";
import biopcia from "../../../assets/images/main/biopcia.png";

const data = [
  {
    id: 1,
    img: spectrocopia,
    text: "Спектроскопия",
  },
  {
    id: 2,
    img: dermotoskopia,
    text: "Дерматоскопия",
  },
  {
    id: 3,
    img: gistologia,
    text: "Гистология",
  },
  {
    id: 4,
    img: biopcia,
    text: "Биопсия",
  },
];
const MainBlog = () => {
  return (
    <section className={styles.section}>
      <div className="container">
        <h2 className={styles.title}>Блог</h2>
        <div className={styles.groupBlocks}>
          {data.map((item) => {
            return (
              <div className={styles.block} key={item.id}>
                <div className={styles.wrapperIcon}>
                  <img src={item.img} alt="" />{" "}
                </div>
                <span className={styles.text}>{item.text}</span>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
};
export default MainBlog;
